"use client";
import React, { useState, useRef, useEffect } from "react";
import { motion, AnimatePresence } from "framer-motion";
import { LampContainer } from "@/components/ui/lamp";
import { PromptInputBox } from "@/components/ui/ai-prompt-box";
import { CodeViewer } from "@/components/ui/code-viewer";
import { TextShimmerWave } from "@/components/ui/text-shimmer-wave";
import { cn } from "@/lib/utils";
import { ThinkingPlanet } from "@/components/ui/thinking-planet";

type Message = {
  id: string;
  role: "user" | "assistant";
  content: string;
  files?: File[];
  isStreaming?: boolean; // Флаг, нужно ли печатать этот текст
};

// Компонент для эффекта печатной машинки
const TypewriterEffect = ({ text, onComplete }: { text: string, onComplete?: () => void }) => {
  const [displayedText, setDisplayedText] = useState("");

  useEffect(() => {
    let i = 0;
    const speed = 20;
    
    const timer = setInterval(() => {
      if (i < text.length) {
        setDisplayedText((prev) => prev + text.charAt(i));
        i++;
      } else {
        clearInterval(timer);
        if (onComplete) onComplete();
      }
    }, speed);

    return () => clearInterval(timer);
  }, [text, onComplete]);

  return <span>{displayedText}</span>;
};

export default function Home() {
  const [isChatStarted, setIsChatStarted] = useState(false);
  const [isCodePanelOpen, setIsCodePanelOpen] = useState(false);
  const [isThinking, setIsThinking] = useState(false); // Состояние "Думает"
  
  const [messages, setMessages] = useState<Message[]>([]);
  const [currentCode, setCurrentCode] = useState("");
  const [currentFileName, setCurrentFileName] = useState("");
  
  const scrollRef = useRef<HTMLDivElement>(null);

  const handleSendMessage = (content: string, files?: File[]) => {
    if (!content.trim() && (!files || files.length === 0)) return;

    if (!isChatStarted) setIsChatStarted(true);

    // 1. Сообщение юзера
    const userMessage: Message = {
      id: Date.now().toString(),
      role: "user",
      content,
      files,
    };
    setMessages((prev) => [...prev, userMessage]);

    // 2. Включаем режим "Думает"
    setIsThinking(true);

    const isCodeRequest = content.toLowerCase().includes("код") || content.toLowerCase().includes("code");

    // 3. Задержка 5 секунд (имитация размышлений)
    setTimeout(() => {
      setIsThinking(false); // Перестаем думать

      let aiContent = "Это обычный ответ. Если хочешь увидеть код, напиши слово 'код'.";
      
      // Подготовка ответа
      if (isCodeRequest) {
        aiContent = "Конечно! Я подготовил для вас компонент кнопки. Обратите внимание на использование утилиты cn для слияния классов. Панель с кодом открыта справа.";
        setCurrentCode(`import React from "react";
import { cn } from "@/lib/utils";

export const Button = ({ className, ...props }) => {
  return (
    <button 
      className={cn(
        "px-4 py-2 bg-blue-500 text-white rounded hover:bg-blue-600 transition", 
        className
      )} 
      {...props} 
    />
  );
};`);
        setCurrentFileName("Button.tsx");
        setIsCodePanelOpen(true);
      }

      const aiMessage: Message = {
        id: (Date.now() + 1).toString(),
        role: "assistant",
        content: aiContent,
        isStreaming: true,
      };
      setMessages((prev) => [...prev, aiMessage]);

    }, 5000); // 5 секунд размышлений
  };

  // Автоскролл
  useEffect(() => {
    if (scrollRef.current) {
      scrollRef.current.scrollTo({ top: scrollRef.current.scrollHeight, behavior: "smooth" });
    }
  }, [messages, isChatStarted, isThinking]);

  return (
    <LampContainer>
      <div className="flex flex-col items-center w-full h-full relative z-50">
        
        {/* ЗАГОЛОВОК */}
        <AnimatePresence>
          {!isChatStarted && (
            <motion.div
              initial={{ opacity: 0.5, y: 100 }}
              animate={{ opacity: 1, y: 0 }}
              exit={{ opacity: 0, filter: "blur(10px)" }}
              transition={{ duration: 0.8, ease: "easeInOut" }}
              className="absolute top-1/2 -translate-y-70 z-10 flex justify-center w-full"
            >
              <h1 className="bg-gradient-to-br from-slate-300 to-slate-500 py-4 bg-clip-text text-center text-4xl font-medium tracking-tight text-transparent md:text-7xl">
                TestOps <br /> Assistant
              </h1>
            </motion.div>
          )}
        </AnimatePresence>

{/* АНИМАЦИЯ РАЗМЫШЛЕНИЯ */}
{isThinking && (
              <motion.div 
                initial={{ opacity: 0, y: 10, scale: 0.95 }} 
                animate={{ opacity: 1, y: 0, scale: 1 }}
                className="flex w-full justify-start"
              >
                 <div className="flex items-center gap-3 px-4 py-3 bg-[#1e293b]/60 border border-lime-500/20 rounded-2xl rounded-bl-none backdrop-blur-sm shadow-[0_0_15px_-5px_rgba(132,204,22,0.3)]">
                    
                    {/* 1. Текст СЛЕВА */}
                    <motion.span 
                      animate={{ opacity: [0.5, 1, 0.5] }}
                      transition={{ duration: 2, repeat: Infinity, ease: "easeInOut" }}
                      className="text-sm font-medium text-lime-400 tracking-wide"
                    >
                      Думаю...
                    </motion.span>

                    {/* 2. Планетка СПРАВА */}
                    {/* h-6 w-6 (24px) - оптимальный размер для строки текста */}
                    <div className="h-6 w-6 relative flex items-center justify-center">
                        <ThinkingPlanet className="w-full h-full" />
                    </div>

                 </div>
              </motion.div>
            )}
        {/* ГЛАВНАЯ ОБЕРТКА */}
        <motion.div 
          layout
          className={cn(
            "flex w-full gap-4 px-4 transition-none",
            isChatStarted 
              ? "h-[85vh] my-auto items-center justify-center" 
              : "h-full items-end justify-center pb-4"
          )}
        >
          
          {/* ЛЕВАЯ КОЛОНКА: ЧАТ */}
          <motion.div
            layout="position"
            transition={{ type: "spring", damping: 25, stiffness: 120 }}
            className={cn(
              "flex flex-col items-center justify-end relative z-20 rounded-3xl overflow-hidden",
              isChatStarted 
                ? cn(
                    "bg-slate-950/50 border border-slate-800 p-4 backdrop-blur-sm shadow-2xl h-full",
                    isCodePanelOpen ? "w-[50%]" : "w-full max-w-4xl"
                  )
                : "w-full max-w-5xl h-auto bg-transparent border-transparent"
            )}
          >
            {/* Область сообщений */}
            {isChatStarted && (
              <motion.div 
                initial={{ opacity: 0 }}
                animate={{ opacity: 1 }}
                transition={{ delay: 0.3 }}
                ref={scrollRef}
                className="flex-1 w-full overflow-y-auto overflow-x-hidden mb-4 p-4 rounded-xl custom-scrollbar space-y-6"
              >
                {messages.map((msg) => (
                  <motion.div
                    key={msg.id}
                    initial={{ opacity: 0, y: 10 }}
                    animate={{ opacity: 1, y: 0 }}
                    className={cn("flex w-full", msg.role === "user" ? "justify-end" : "justify-start")}
                  >
                    <div className={cn(
                      "max-w-[85%] px-5 py-3 rounded-2xl text-base leading-relaxed shadow-sm",
                      msg.role === "user"
                        ? "bg-[#1e40af]/30 text-blue-100 border border-blue-500/20 rounded-br-none"
                        : "bg-[#1e293b]/60 text-slate-200 border border-slate-700/50 rounded-bl-none"
                    )}>
                      {/* Если это ИИ и флаг стриминга включен - печатаем, иначе показываем сразу */}
                      {msg.role === "assistant" && msg.isStreaming ? (
                        <TypewriterEffect text={msg.content} />
                      ) : (
                        msg.content
                      )}
                    </div>
                  </motion.div>
                ))}

                {/* АНИМАЦИЯ РАЗМЫШЛЕНИЯ */}
                {isThinking && (
                  <motion.div 
                    initial={{ opacity: 0, y: 10 }} 
                    animate={{ opacity: 1, y: 0 }}
                    className="flex w-full justify-start"
                  >
                     <div className="bg-[#1e293b]/60 border border-slate-700/50 px-5 py-3 rounded-2xl rounded-bl-none flex items-center gap-2">
                        <TextShimmerWave 
                          className="text-sm font-medium [--base-color:#4d7c0f] [--base-gradient-color:#bef264]"  
                          duration={1}
                        >
                          Думаю...
                        </TextShimmerWave>
                     </div>
                  </motion.div>
                )}

              </motion.div>
            )}

            {/* Инпут */}
            <motion.div layout="position" className="w-full">
              <PromptInputBox 
                onSend={handleSendMessage}
                isLoading={isThinking} // Блокируем инпут пока думает
                placeholder={isCodePanelOpen ? "Обсудить код..." : "Спроси меня о чем угодно..."}
                className={cn(isChatStarted && "border-slate-700 shadow-none bg-slate-900/50")}
              />
            </motion.div>
          </motion.div>

          {/* ПРАВАЯ КОЛОНКА: CODE VIEWER */}
          <AnimatePresence mode="popLayout">
            {isChatStarted && isCodePanelOpen && (
              <motion.div
                initial={{ opacity: 0, width: 0, x: 20 }}
                animate={{ opacity: 1, width: "50%", x: 0 }}
                exit={{ opacity: 0, width: 0, x: 20 }}
                transition={{ type: "spring", damping: 25, stiffness: 120 }}
                className="h-full flex-shrink-0"
              >
                <CodeViewer 
                  code={currentCode} 
                  filename={currentFileName}
                  onClose={() => setIsCodePanelOpen(false)}
                />
              </motion.div>
            )}
          </AnimatePresence>

        </motion.div>
      </div>
    </LampContainer>
  );
}